"use client"; // karena kita pakai state dan efek

import './globals.css';
import { Inter } from 'next/font/google';
import Sidebar from '@/app/components/Sidebar';
import Link from "next/link";
import { LogIn, LogOut } from "lucide-react";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";

const inter = Inter({ subsets: ['latin'] });

export const metadata = {
  title: 'Info Kelas - 2IA15',
  description: 'Aplikasi manajemen kelas kuliah pribadi.',
};

export default function RootLayout({ children }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const router = useRouter();

  // Cek login saat mount
  useEffect(() => {
    const token = localStorage.getItem('token'); // atau bisa cek 'role'
    setIsLoggedIn(!!token);
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token'); // hapus token atau role
    localStorage.removeItem('role');
    setIsLoggedIn(false);
    router.push('/login'); // redirect ke login
  };

  return (
    <html lang="id">
      <body className={inter.className}>
        <div className="flex min-h-screen bg-gray-100">
          {/* Sidebar */}
          <Sidebar /> 
          
          {/* Konten Utama */}
          <div className="flex-1 flex flex-col overflow-hidden">
            <header className="w-full bg-white shadow p-4 sticky top-0 z-10 flex items-center">
              <h1 className="text-xl font-bold text-gray-800">Info Kelas - 2IA15</h1>

              {/* Tombol Login / Logout */}
              {isLoggedIn ? (
                <button
                  onClick={handleLogout}
                  className="ml-auto px-4 py-2 border border-gray-300 text-gray-700 rounded-full 
                             hover:bg-gray-200 hover:border-gray-400 transition flex items-center gap-2"
                >
                  <LogOut size={16} />
                  Logout
                </button>
              ) : (
                <Link 
                  href="/login"
                  className="ml-auto px-4 py-2 border border-gray-300 text-gray-700 rounded-full 
                            hover:bg-gray-200 hover:border-gray-400 transition flex items-center gap-2"
                >
                  <LogIn size={16} />
                  Login
                </Link>
              )}
            </header>

            <main className="flex-1 overflow-x-hidden overflow-y-auto p-6 md:p-8">
              {children} 
            </main>
          </div>
        </div>
      </body>
    </html>
  );
}
